
.Main{
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
  text-align: center;
  font-family: 'Lust Script Display', sans-serif;
  color: #dcd09c; /*color of navigation links*/
  
}

.IframeTop {
  padding-bottom: 56.25%;
  height: 0;
  overflow: hidden;
  position: relative;
  
}

.IframeWrapper {
  position: relative;
  padding-bottom: 200%;
  -webkit-transform: translateY(-35.95%);
          transform: translateY(-35.95%);
}

iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.LinkWrapper {
	position: absolute;
	bottom: 5em;
	width: 100%;
}

.LinkWrapper * {
	margin: 0 2em;
	font-size: 30px;
	font-weight: bold;
}

.bgImage {
  display: block;
  width: 100vw;
  height: 100vh;
  object-fit: cover; 
}
.Music {
  background: url("/bgmusic.jpg") no-repeat center #000;
	background-size: cover;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  position: fixed;
}

.Music > * {
  width: 50%;
  min-width: 650px;
  margin: 0 auto;
}

.Music > .react-pdf__Document {
  margin-top: 5rem
}

.Logo {
  background: url("/musiclogo.png") no-repeat center transparent;
	background-size: contain;
  margin-top: 10rem;
  height: 50%;
  top: 0;
  left: 0;
  position: relative;
}

.MusicLinks {
  position: relative;
  top: 0;
  color: yellow;
  width: calc(150px + 30%);
  text-align: center;
}

.MusicLinks a, .MusicLinks span {
  position: relative;
  margin: 0 auto;
  cursor: pointer;
  font-size: 24px;
  color: yellow;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.MusicLinks span {
  top: 1rem;
  padding-top: 1rem;
  padding-bottom: 5rem;
  font-size: 18px;
}

.MusicLinks a:first-child, .MusicLinks span:first-child {
  margin-right: 10rem;
}
.MusicLinks a:last-child, .MusicLinks span:last-child {
  margin-left: 10rem;
}

.mDownload {
  position: absolute;
  top: calc(4rem - 18px);
  left: 0;
  width: 100%;
  color: yellow;
  text-align: center
}

.mDownload a {
  font-size: 18px;
}

.mBack {
  position: absolute;
  top: 0;
  left: 0;
  width: auto;
  margin: 2rem;
  font-size: 24px;
  color: yellow;
}

.mBack a {
  font-size: 18px;
}

